.box__button {
  width: 30%;
}

.box__button button {
  display: block;
  width: 95%;

  margin: 5px auto 5px;
  padding: 5px;

  color: #739ca5;
  background-color: #2c4055;
  border: none;

  font-size: 18px;
}

.box__button button:hover {
  box-shadow: inset 0 0 5px #739ca5;
}

@media (max-width: 794px) {
  .little_left_box {
    width: 100%;
  }

  .box__button {
    width: 100%;
  }

  .box__button button {
    width: 100%;
  }
}
