.title {
  text-align: center;
  font-size: 48px;

  margin-bottom: 20px;
}

.box {
  display: flex;
  /* justify-content: space-between; */
  width: 90%;
  height: 100%;

  margin: 0 auto;
  padding: 5px;
}

.little_left_box {
  width: 70%;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 794px) {
  .box {
    display: block;
    width: 95%;
  }

  .little_left_box {
    width: 100%;
  }
}
