.box__ul {
  width: 50%;
}

.li__true {
  display: block;
  max-width: 90%;
  max-height: 28px;
  min-height: 28px;

  margin: 5px auto 5px;
  padding: 5px;

  background-color: green;
  list-style-type: none;

  white-space: nowrap;
  overflow: hidden;
  font-size: 18px;

  border-radius: 12px;

  color: #ffffff;
  box-shadow: inset 0 0 15px yellow;
}

.li {
  display: block;
  max-width: 95%;
  max-height: 28px;
  min-height: 28px;

  margin: 5px auto 5px;
  padding: 5px;

  background-color: #1e2c3a;
  list-style-type: none;

  white-space: nowrap;
  overflow: hidden;
  font-size: 18px;

  border-radius: 12px;
  transition: all 0.1s;
}

.li:hover {
  box-shadow: inset 0 0 5px #739ca5;
}

@media (max-width: 794px) {
  .li {
    max-width: 95%;
  }
}
